  /**
   * Create form subArray 
   */


  getSubArray(): FormGroup {             //^[a-zA-Z \-#,@\']+
    return this.fb.group({
      columnnName: ['', [Validators.required]],
      dataType: [null, [Validators.required]],
      mappingColumn: ['', [Validators.minLength(1), Validators.maxLength(3), Validators.pattern('^[a-zA-Z \-#,@,%\']+')]],
      mandatory: false,
      headerName: [''],
      dateFormat: [{ value: '', disabled: true }],
      authStatus: [''],
      modifiedBy: [this.currentUser.userId],
      authorizedBy: [''],
      version: [''],
      modifiedTime: [''],
      authorizedTime: [''],
      recordStatus: [''],
      delimeter: [''],
      defaultValue: [''],
      repeatedTillNextValue: ['']

    });
  }


  /**
   * Add new subArray row into form
   */

  addSubArray() {
    const control = <FormArray>this.addForm.controls['subArray'];
    control.push(this.getSubArray());
  }

  /**
   * Remove subArray row from form on click delete button
   */

  removeSubArray(i: number) {
    const control = <FormArray>this.addForm.controls['subArray'];

    control.removeAt(i);
  }


  /**
 * This is one of the way how clear subArray fields.
 */

  clearAllSubArray() {
    const control = <FormArray>this.addForm.controls['subArray'];
    while (control.length) {
      control.removeAt(control.length - 1);
    }
    control.clearValidators();
  }


