// =====================Db connection Table==============
onsearchingTablName(event:any)
{
  let data = this.tableNameinc.filter(function (e) {
   
    let tableName = e.toLowerCase();
  
    let value = event.target.value.toLowerCase();
    return !tableName.indexOf(value);
  });
  this.incDbRecords = data;
}
