  systemId:any;
  messageType:any;
  messageChannel:any;
  messageFormat:any;
  queueType:any;
  queueManager:any;
  queueName:any;
  queueChannel:any;
  queueConnFactory:any;
  dbUsername:any;
  dbPassword:any;
  dbServiceName:any;
  dbConnectString:any;
  sysService:Array<MdmtSystemService>
  


