  allowNumbersOnly(n) {
    
    const pattern = /[0-9\+\-\ ]/;
  let inputChar = String.fromCharCode(n.charCode);

      if (!pattern.test(inputChar) && n.charCode != '0') {
          n.preventDefault();
      }
}

