onEdit()
{
  console.log(this.addSysBasic.approvedStatus);
  if(this.addSysBasic.approvedStatus== "U"){
    this.isEdit=true;
    Swal.fire("UnAuthorized record can't be edited !");
  }
  this.isEdit=false;
  if(this.addSysBasic.recordStatus== "C"){
    this.isEdit=true;
    Swal.fire("Closed record can't be edited.");
  }
}
