import { Component, OnInit, ViewChild, ViewChildren, QueryList } from '@angular/core';
import { ExcelExtsys } from '../excel-external-sys/excelExtSys';
import { MatPaginator, MatTableDataSource, MatSort } from '@angular/material';

import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { ExcelProcessingService } from 'src/app/shared/services/sias2-services/excel-processing.service';
import { ExcelApiService } from 'src/app/shared/services/sias2-services/ExcelApiService';
import { UsersService } from '../../users/users.service';
import { ExcelService } from 'src/app/shared/services/excel.service';
import { RoleService } from '../../roles1/roles.service';
import { permissionsLabels } from '../../roles1/models/fmosNewRolePermissions';


@Component({
  selector: 'app-extsummary',
  templateUrl: './extsummary.component.html',
  styleUrls: ['./extsummary.component.scss']
})
export class ExtsummaryComponent implements OnInit {

  @ViewChild(MatSort, { static: true }) sort: MatSort;
  displayedColumns: string[] = ['extSysCode', 'extSysName', 'authStatus', 'modifiedTime'];
  // dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  //ExcelExtsys
  formData: ExcelExtsys;
  extdata: ExcelExtsys[];
  dataSource: any;
  pageSize: any;
  pageIndex: any;
  dataIndex: any;
  user_id: any;
  screenName: any;
  role: any;
  res: any;
  rolepermission: any;
  button_permission: any = {};
  @ViewChildren(MatPaginator) paginator = new QueryList<MatPaginator>();
  roleCodes = new permissionsLabels();
  ngOnInit() {
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message)
    this.user_id = sessionStorage.getItem('user_id');
    this.role = sessionStorage.getItem('user_role');

    this.getExt();
    // this.screenpermission();
  }
  newRolePermissions() {
    this.roleService.fetchScreenPermissions('External Systems');
  }
  screenpermission() {
    this.screenName = 'ExternalSystem';
    // this.role='ROLE1';
    this.userApi.getRoleScreenPermission(this.user_id, this.screenName, this.role).subscribe(res => {
      this.rolepermission = res;
      //console.log(this.rolepermission);
      if (this.rolepermission) {
        this.button_permission = this.rolepermission[0];
        //console.log( this.button_permission);
      }
    });

  }

  constructor(private api: ExcelProcessingService, private route: ActivatedRoute,
    private http: HttpClient,
    private router: Router,
    private userApi: UsersService,
    private excelService: ExcelService,
    private roleService: RoleService,
    private apiService: ExcelApiService) {
    // this.route.queryParams.subscribe(params => {

    //   // this.modifyUserObject.userRef = params["userRef"];
    //   this.formData.extSysCode = params['extSysCode'];
    //   this.formData.extSysName = params['extSysName'];
    // });
  }


  getExt() {
    this.api.getAllExt().subscribe(data => {
      this.extdata = data;
      console.log(data);
      this.dataSource = new MatTableDataSource(
        data
      );
      this.dataSource.sort = this.sort;
      this.dataSource.paginator = this.paginator.toArray()[0];
      //console.log(data);
    });
    // //console.log(this.extdata);
  }
  getExtSys(row) {
    //console.log("row", row);
    this.apiService.externalSystem.next(row);

    const navigationExtras: NavigationExtras = {
      queryParams: {
        
      }
    };

    this.router.navigate(['/sias-v2/excelExtSysUpdate'], navigationExtras);
  }
  onPaginateChange(event) {
    //console.log(event);
    // //console.log(this.pageSize);
    this.pageSize = event.pageSize;
    this.pageIndex = event.pageIndex;
    //console.log(event.pageSize);
    //console.log(event.pageIndex);
  }
  exportAsXLSX(): void {
    // this.allUsersList.forEach(element => {
    //   if(element.creatorDtStamp!==null && element.verifierDtStamp !==null){
    //   element.creatorDtStamp
    //   =this.pipe.transform(element.creatorDtStamp        , 'dd-MMM-yy').toString();
    //   element.verifierDtStamp
    //   =this.pipe.transform(element.verifierDtStamp
    //     , 'dd-MMM-yy').toString();
    //   }
    // });
    this.excelService.exportAsExcelFile(this.extdata, 'External_System_Report');
  }
}

