import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { RulesService } from 'src/app/shared/services/rules/rules.service';
import { Router } from '@angular/router';
import { AddSystemService } from 'src/app/shared/services/addSys/add-system.service';

@Component({
  selector: 'app-list-rule-mapping',
  templateUrl: './list-rule-mapping.component.html',
  styleUrls: ['./list-rule-mapping.component.css']
})
export class ListRuleMappingComponent implements OnInit {
  ruleMappingListResp: any;

  constructor(private rulelist:RulesService,private router:Router,private addSystem:AddSystemService) { }
  displayedColumns: string[] = ['sourceId', 'targetTranslationId', 'recordStatus', 'creatorId','createdTime'];
  dataSource: MatTableDataSource<any>;
  @ViewChild(MatPaginator,{static:true}) paginator: MatPaginator;
  @ViewChild(MatSort,{static:false}) sort: MatSort;

  ngOnInit() {
    this.fetchinglistRuleMapping()
  }
  fetchinglistRuleMapping(){
    this.rulelist.fetchingruleMapping().subscribe(resp=>{
      console.log(resp);
      this.ruleMappingListResp=resp;
      // this.setForm(this.sumaData);
      this.dataSource = new MatTableDataSource(this.ruleMappingListResp);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }
  ruleMappingEdit(row)
  {
    console.log(row);
    let id : any = 0;
    if(row.msgMappingDetails.length > 0){
      id = row.msgMappingDetails[0].messageMappingId;
    }
    this.rulelist.getruleMappingById(id,row.mappingId).subscribe(resp=>{
      console.log(resp);
      let queryParams = {
        'sourceTransDetId':resp.sourceTransDetId,
        'targetTransDetId':resp.targetTransDetId,
        'mdmtMessageMappingDet':resp.mdmtMessageMappingDet,
        'responseData':row
    }
    console.log(queryParams);
    
   
    this.addSystem.sendNavParam(queryParams);
    this.router.navigateByUrl('/sias-v2/editRuleMapping');
    })
   
  }
}
