import { Component, OnInit, ViewChild } from '@angular/core';
import { RulesService } from 'src/app/shared/services/rules/rules.service';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { AddSystemService } from 'src/app/shared/services/addSys/add-system.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-rule-translationsummary',
  templateUrl: './rule-translationsummary.component.html',
  styleUrls: ['./rule-translationsummary.component.css']
})
export class RuleTranslationsummaryComponent implements OnInit {
  displayedColumns: string[] = ['id', 'systemCode', 'operationName', 'serviceName', 'creatorId','createdTime'];
  translationList:any;
  translationDeatils: Array<translationDeatils> = [];
  dataSource: MatTableDataSource<any>;
  @ViewChild(MatPaginator,{static:true}) paginator: MatPaginator;
  @ViewChild(MatSort,{static:false}) sort: MatSort;
  constructor(private ruleServ:RulesService,private addSystem:AddSystemService,private router:Router) { }

  ngOnInit() {
    this.fetchingruleTranslationSummary()
  }
  fetchingruleTranslationSummary(){
    this.ruleServ.fetchingruleTranslation().subscribe(resp=>{
      console.log(resp);
      if(resp)
      {
        this.translationList=resp;
        
        // this.setForm(this.sumaData);
        this.dataSource = new MatTableDataSource(this.translationList);
        console.log("datasource",this.dataSource);
        // this.dataSource.forEach(el => {
        //   if(el.messsageType=='I')
        //   {
        //     el.messsageType=='Incoming'
        //   }
        //   else if(el.messsageType=='O')
        //   {
        //     el.messsageType=='Outgoing';
        //   }
        //   else{
        //     el.messsageType=='All'
        //   }
        // });
        this.dataSource.sort = this.sort;
        this.dataSource.paginator = this.paginator;
      }
    })
  }
  oneditBasedOnId(row)
  {
    console.log(row);
    this.ruleServ.ruleTranslationBasedonId(row.transId).subscribe(resp=>{
      console.log(resp);
      this.translationDeatils=resp;
    
    let queryParams = {
      'translationDeatils':resp,
      'ruleTRespData':row,
      'systemCode':row.systemCode,
      'messageType':row.messageType,
      'operationName':row.operationName,
      'serviceName':row.serviceName
  }
  // console.log(queryParams);

 
  this.addSystem.sendNavParam(queryParams);
  this.router.navigateByUrl('/sias-v2/editTranslation');
});
  }
}
export class translationDeatils {
  id:any;
  systemChannelId:any;
  messageBasis:any;
  messageKey:any;
  dataType:any;
  encryption: any;
  headerTag:any;
  headerValue:any;

}

