  matlabel2: string;
  navSubscription: any;
  editRuleTransResp: any;
  headerTagData: any;
  messageBasisIso: any;
  messageKeyIso: any;
  xmlRespData: any;
  fileName: any;
  editValue: boolean;
  afterupload: boolean;
  showdelete: boolean;
  deleted: boolean;
  editBtn: boolean;
  closeBtn: boolean;
  authBtn: boolean;
  onlysubmit: boolean;
  reopenBtn: boolean;
