import { FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MatPaginator } from '@angular/material';

@Component({
  selector: 'app-source-destination-system-validation',
  templateUrl: './source-destination-system-validation.component.html',
  styleUrls: ['./source-destination-system-validation.component.css']
})
export class SourceDestinationSystemValidationComponent implements OnInit {
  options: string[] = ['Sys1', 'System2', 'System3', 'Sys4', 'Sys5'];
  options1: string[] = ['Sys1', 'System2', 'System3', 'Sys4', 'Sys5'];
  filteredOptions: Observable<string[]>;
  filteredOptions1: Observable<string[]>;
  sourceDestinationSystemForm: FormGroup;
  isSourceDestination:boolean=true;
  isSrcDestValidation:boolean=false;
  dataSource : any;

  searchField;
 
  displayedColumns: string[] = ['source', 'destination','validationMethod', 'details','values'];
  constructor(private formBuilder: FormBuilder) { }
  @ViewChildren(MatPaginator) paginator = new QueryList<MatPaginator>();
  ngOnInit() {

    this.sourceDestinationSystemForm = this.formBuilder.group({
      sourceSystem: [''],
      destinationSystem: ['']
    });

    this.filteredOptions = this.sourceDestinationSystemForm.controls['sourceSystem'].valueChanges.pipe(
      startWith(''),
      map(value => this._filterSource(value))
    );

    this.filteredOptions1 = this.sourceDestinationSystemForm.controls['destinationSystem'].valueChanges.pipe(
      startWith(''),
      map(value => this._filterDestination(value))
    );
    this.dataSource=ELEMENT_DATA;
  
    this.dataSource.paginator = this.paginator.toArray()[0];
  }

  private _filterSource(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.options.filter(option => option.toLowerCase().includes(filterValue));
  }
  private _filterDestination(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.options1.filter(option => option.toLowerCase().includes(filterValue));
  }

  submit(sourceDestinationSystemForm) {
    console.log(sourceDestinationSystemForm);
    this.isSourceDestination=false;
    this.isSrcDestValidation=true;
  }

 
}
  
export interface PeriodicElement {
  destination: string;
  source: String;
  validationMethod:String;
  details:String;
  values:String;

  
}

const ELEMENT_DATA: PeriodicElement[] = [
  {source: 'system1', destination: 'system4',validationMethod: 'SOURCE METHOD',details:'MAPPING OF SOURCE METHOD',values:'123456' },
  {source: 'system2', destination: 'System5',validationMethod: 'DESTINATION METHOD',details:'MAPPING OF DESTINATION METHOD',values:'456789' },
  {source: 'system1', destination: 'system4',validationMethod: 'SOURCE METHOD',details:'MAPPING OF SOURCE METHOD',values:'123456' },
 
 
];