import { Injectable } from '@angular/core';
import { HttpClient, HttpEvent, HttpRequest, HttpEventType } from '@angular/common/http';
import { Observable } from 'rxjs';
import { AppConstants } from '../../config/app.constant';

//export const median = 'http://localhost:9191';
//export const median = 'http://192.168.0.119:9191';
export const median = AppConstants.baseURL;
// export const median = 'http://10.148.34.13:9123/excel';

export const API_URL = 'http://localhost:8889';


@Injectable({
  providedIn: 'root'
})
export class ApiService {

  FILE_URL = 'http://localhost:8777';
  VMEXTSYSTEMS = 'http://localhost:8080/batchcontroller/start';

  constructor(private httpClient: HttpClient) { }

  pushFileToStorage(file: File): Observable<HttpEvent<{}>> {

    const formdata: FormData = new FormData();
    formdata.append('file', file);
    const req = new HttpRequest('POST', `${this.FILE_URL}/upload`, formdata, {
      reportProgress: true,
      responseType: 'text'

    }
    );
    return this.httpClient.request(req);
  }

  pushFileToStorageQueueStatus(file: File): Observable<HttpEvent<{}>> {


    const formdata: FormData = new FormData();
    formdata.append('file', file);
    const req = new HttpRequest('POST', `${this.FILE_URL}/editedFile`, formdata, {
      reportProgress: true,
      responseType: 'text'

    }
    );
    return this.httpClient.request(req);
  }

  financeUploadService(file: File, reportName, Exec_Date): Observable<HttpEvent<{}>> {
    const formdata: FormData = new FormData();
    formdata.append('file', file);
    const req = new HttpRequest('POST', `${this.FILE_URL}/financeUpload/${reportName}/${Exec_Date}`, formdata, {
      reportProgress: true,
      responseType: 'text'

    }
    );
    return this.httpClient.request(req);
  }

  financeDownloadService(reportName, Exec_Date) {
    return this.httpClient.get<any[]>(`${this.FILE_URL}/financeDownload/${reportName}/${Exec_Date}`);
  }

  dynamicReportService(reportName, Exec_Date) {
    return this.httpClient.get<any[]>(`${this.FILE_URL}/financeDownload3d/${reportName}/${Exec_Date}`);
  }

  // transferingUserObject(user:User)
  // {
  //   if(user.userType == 'N')
  //   {
  //     getUserObject(user);
  //   }
  // }
}
