import { Component, OnInit, ViewChild } from '@angular/core';
import { ExcelProcessingService } from 'src/app/shared/services/sias2-services/excel-processing.service';
import { MatPaginator, MatTableDataSource, MatSort } from '@angular/material';
import { permissionsLabels } from 'src/app/views/roles1/models/fmosNewRolePermissions';
import { RoleService } from 'src/app/views/roles1/roles.service';
import { NavigationExtras, Router } from '@angular/router';






@Component({
  selector: 'app-excel-mapping-summary',
  templateUrl: './excel-mapping-summary.component.html',
  styleUrls: ['./excel-mapping-summary.component.css']
})
export class ExcelMappingSummaryComponent implements OnInit {
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  data: any;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  dataSource: any;
  roleCodes = new permissionsLabels();
  displayedColumns: string[] = [
    "extSysCode",
    "extSys",

    'inputBy',
    'authStatus',
    'inputDtStamp',
   
  ];
  constructor(private excelUpload: ExcelProcessingService,
    private router: Router, private roleService: RoleService) {

  }

  ngOnInit() {
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    this.getDataForSummary();
  }
  newRolePermissions() {
    this.roleService.fetchScreenPermissions('Excel Mapping');
  }
  getDataForSummary() {
    this.excelUpload.getMappingSummary().subscribe(data => {
      this.data = data;
      console.log(this.data);
      this.dataSource = new MatTableDataSource<any>(this.data);
      this.dataSource.sort = this.sort;
      this.dataSource.paginator = this.paginator;
    })

  }
  getDataFromSummaryToUpdateExcelMapping(row) {
    console.log("this is row", row)




    const navigationExtras: NavigationExtras = {
      queryParams: {
        extSysCode: row.extSysCode,
        processName: row.processName,
        extSysName: row.extSys
      }

    };

    this.router.navigate(['sias-v2/updateExcelMappingComponent'], navigationExtras);
  }
}
